.question {
 background-color: rgb(206, 198, 198);
 border-left: 5px solid rgb(59, 59, 204);
 width: 80%;
 margin:10px auto 15px 60px;
 padding: 5px 10px;
}

.question h4 {
  font-size: 1.2rem;
  line-height: 1.5;
  text-align: center;
 }

 .question .options-container{
 display: inline-block;
 width:50%;
 }

 .question .options-container .option{
  background-color: rgb(175, 172, 167);
  border-radius: 10px;
  color: rgb(3, 3, 3);
  cursor:pointer;
  margin: 5px auto;
  padding: 1rem;
  transition: 0.3s linear all;
  text-align:left;
  width:200%;

 }
 .question .options-container .option:hover {
  background-color:rgb(113, 117, 121)
 }

 .question .button-container {
  display: flex;
  justify-content: flex-start;
  margin:0 auto 5px auto;
  padding: 10px;
  width: 80%;
 }

 .question .button-container button {
  border: none;
  color:bisque;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  margin-right: 10px;
  padding: 3px 5px;
 }

 .question .button-container button:first-child{
  background-color: darkslategrey;
  box-shadow: 0 2px 2px 0 rgb(0, rgb(29, 46, 29), rgb(45, 45, 95), 0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2));
  transition: 0.2s linear all;
 }
 .question .button-container button:first-child:hover{
   background-color:rgb(41, 18, 18);
 }

 .question .button-container button:nth-child(2){
  background-color: rgb(19, 49, 49);
  box-shadow: 0 2px 2px 0 rgb(0, rgb(29, 46, 29), rgb(45, 45, 95), 0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2));
  transition: 0.2s linear all;
 }
 .question .button-container button:nth-child(2):hover{
   background-color:rgb(58, 20, 20);
 }

 .disable {
  background-color: #ccc !important;
  box-shadow: none !important;
  opacity: 0.9 !important;
  pointer-events: none;

 }